import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Epic, PanelHeader, Panel } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from "./containers/Home";
import Profile from "./containers/Profile";
import Transaction from "./containers/Transaction";
import Top from "./containers/Top";
import About from "./containers/About";
import Stories from "./containers/Stories";


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}
	
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	
componentDidMount() { 
	connect.subscribe((k) => { 
	   switch (k.detail.type) { 
	case 'VKWebAppFlashGetInfo': 
		  this.setState({ hm: k.detail.data }); 
	   break; 
			 default: 
	console.log(k.detail.type); 
} 
}); 
connect.send("VKWebAppFlashGetInfoResult",{} );
}
	 componentDidMount() { 
             connect.subscribe((k) => { 
                switch (k.detail.type) { 
             case 'VKWebAppFlashSetLevelResult': 
                   this.setState({ hm: k.detail.data }); 
                break; 
                      default: 
             console.log(k.detail.type); 
        } 
      }); 
	  connect.send("VKWebAppFlashSetLevel", {"level": 0});
     }


	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home"/>
				<Top id="top"/>
				<Profile id="profile" />
				<Transaction id="transaction"/>
				<About id="about"/>
				<Stories id="stories"/>
				</View>
		);
	}
}
export default App;
