import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, List, Avatar, Cell, Button, Div, FormStatus, Header } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24Message from '@vkontakte/icons/dist/24/message';
import { fireEvent } from "../utils";
const Stories = ({ id, back, top, openProfile }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
                Истории
            </PanelHeader>
            <Group>
                <List>
                    {top.map((item) => {
                        return (
                            <Cell
                                before={<Avatar src={item.avatar}/>}
                                description={"Рейтинг: " + item.carma}
                                onClick={() => openProfile(item.id)}
                            >
                                {item.first_name + " " + item.last_name}
                            </Cell>
                        );
                    })}
                </List>
                <Group>
           
                <Div>
      Возникла у меня проблема с мобильным, потерялись почти все номера в телефонной книге. Делаю по знакомым рассылку (по почте): так, мол, и так, скинь, плиз, мне на моб смс, что это ты...

      Ну что, теперь у меня в мобильном штук 20 смс с текстом: "Привет, это я!"
      </Div>
      
      <Div>
      Маршрутка. Водитель ждет, пока салон заполнится, не трогается с места.
      Нервная пожилая дама:
     — Водитель! Сколько можно стоять? Я уже полчаса тут сижу!
      Водитель:
     — Можете идти по маршруту. Я вас по дороге подхвачу. 
      </Div>
     
      <Div>
      Однажды в походе, когда все уже перепились, один товарищ заснул сладким сном прямо под ёлкой. Тогда мы осторожненько надели ему на руку трёхлитровую стеклянную банку — и пощекотали нос соломинкой…
     Результат был ужасен… 
      </Div>
     
      <Div>
      Поменял тут батарею на кухне. Старую чугунную вытащил в подъезд. А этаж пятый, без лифта. Протащил кое-как один лестничный пролет и сдох. И туту меня возникла гениальная идея. Зашел домой, на листочке пишу «кто донесет батарею до улицы, тому 100р. Обращаться в кв. такую-то» Пока писал, батарею сп*здили… 
      </Div>
     
      <Div>
      По зиме было, стою на перекрестке, рядом машина. Тут в нее сзади аккуратненько так, слегка прикоснувшись, въезжает другая. Мужик въехавший испугался наверно, а потом тормоз отпустил и еще раз так же легонечько. Тут из первой машины высовывается водитель и кричит:
    -  Ну, хули ты стесняешься? Разгонись, да е..ни как следует!
      </Div>
      
      <Div>
      Когда сотрудники фирмы, где я работаю, совершают какой-нибудь локальный подвиг, и шеф доволен, он всегда говорит такую фразу: "Ну, молодец, готовь дырочку". Типа, для ордена, в одежде пробивать. Недавно к нам пришла работать новая девушка, которая в один из первых дней получила точно такую жепохвалу. Большие удивленные глаза, невнятное бормотание, осторожный тихий вопрос:
    - Что вы имеете в виду?...
      </Div>
      
      <Div>
      Еду в маршрутке. Рядом мама с ребенком лет 4-5. Ребенок начинает читать стишок:
    - Мама спит, она устала…
    Мамаша, преисполненная гордости за ребенка, расплывается улыбкой, а ребенок продолжает:
    - … а у папы писька встала, чтобы письку опустить, надо маму разбудить.
      </Div>
    
      <Div>
      Желая восстановить пошатнувшуюся гармонию в семейных отношениях, я, следуя авторитетным советам из женских журналов, во время ужина в ресторане наклонилась к мужу и томно прошептала:
    - Милый, я сегодня забыла надеть трусики....
    На что супруг, ошарашенно на меня глядя, произнес:
     - Лена, ты что дура? Как можно ЗАБЫТЬ надеть трусы?!!!
      </Div>
      
      <Div>
      В Сургуте девушка вся из себя такая заводит свою машину и после прогрева начинает сдавать назад, выезжая из тесноты двора. В то же время во втором ряду прогревается другая машина. Девушка, не соблюдая никаких правил, сдает назад и бьет эту машину. Вылезает из машины и, памятуя, что лучшая оборона - это наступление, начинает громко орать на весь двор, обвиняя хозяина побитой машины во всех грехах и в том, что он не умеет водить машину. При этом она бегает вокруг побитой машины и ждет, когда оттуда кто-нибудь вылезет. Но в ответ – тишина. Весь смак в том, что в салоне никого не было - хозяин автомобиля завел его и ушел домой. Со стороны смотрелось очень комично.
      </Div>
     
      <Div>
      2006 год. Чемпионат мира по футболу. Начало трансляции матча 1/4 финала Украина-Италия. Исполняется гимн Украины и камера ползёт по лицам игроков. Сидим всей семьёй перед телевизором и гордость переполняет наши сердца. Жена говорит, глядя на игроков: "Орлы!". Я уточняю: "Соколы!". Сын 5 лет, глядя с интересом на экран, выдает свой вердикт: "Дятлы!" 
      </Div>
      
      <Div>
      Как известно, выборы полны мелкими и забавными курьезами. Один из них произошел в Петрозаводске. Наш кандидат пользовался очень высоким доверием у большого количества бабушек. Одна из них решила помочь ему в расклейке листовок. И вот что она однажды заявила одному из наших штабистов (передаю максимально дословно):
   - Я вот поклеила однажды фотографию (листовку) Сашеньки, а ее какие-то гады вечером сорвали. Я еще раз ее поклеила, а ее снова сорвали. В конце концов, я снова наклеила листовку, а по краям ее, чтоб оторвать не смогли, обмазала говном. И вот уже третий день висит, никто не срывает!
      </Div>
      <Group title="Полезные ссылки">
                <List>
                    <Cell
                        expandable
                        onClick={() => fireEvent("https://vk.com/pukhn_seo")}
                        before={<Icon24Message fill="#4bb34b" />}
                    >
                        <span style={{ color: "#4bb34b" }}>Предложить историю</span>
                    </Cell>
                </List>
            </Group>
    </Group>
    <Header level="2">
        Истории обновляются каждый день. 
      </Header>
    </Group>
   


  

        </Panel>
    );
};
const mapProps = (state) => ({
    top: state.top
}); 

const mapDispatch = ({ navigator: { goBack }, user: { openProfile } }) => ({
    back: goBack,
    openProfile
});

export default connect(mapProps, mapDispatch)(Stories);